.form {
  display: flex;
  flex-direction: column;
  margin: 6rem auto;
  width: 80%;
  justify-content: center;
  align-items: center;
  margin-top: 4rem;
}

input {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  letter-spacing: 1.4px;
}

input[type="text"],
select,
textarea {
  width: 50%;
  padding: 12px;
  border: 1px solid #ccc;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
  border-radius: 0.5rem;
}

input[type="submit"] {
  background-color: #04aa6d;
  color: white;
  padding: 12px 20px;
  border: none;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  input[type="text"],
  select,
  textarea {
    width: 100%;
  }
}
